import { baseApiUrl, showError, userKey } from '../../global'
import axios from 'axios'
export default {
  props: ['usuario'],
  data() {
    return {
      show1: false,
      cadastro: false,
      dialog: false,
      email: '',
      error: '',
      rules: {
        required: (value) => !!value || 'Digite seu e-mail',
        email: (value) => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'E-mail Invalido.'
        },
      },
    }
  },

  methods: {
    signin() {
      axios
        .post(`${baseApiUrl}/signin`, this.usuario)
        .then((res) => {
          this.$store.commit('setUser', res.data)
          localStorage.setItem(userKey, JSON.stringify(res.data))
          this.$router.push({ path: `/perfil/${res.data.user}` })
        })
        .catch(showError)
    },
    forgotPassword() {
      axios
        .put(`${baseApiUrl}/forgotPassword`, {
          email: this.email,
        })
        .then((response) => {
          this.error = response
          this.email = ''
        })
        .catch((error) => {
          this.error = error.response.data
        })
    },
  },

  computed: {
    GetCadastro() {
      return this.$store.state.cadastro
    },

    cad: {
      get() {
        return this.$store.state.cadastro
      },
      set(valor) {
        this.$store.commit('setCadastro', valor)
      },
    },
  },
}
