import { mapState } from 'vuex'
export default {
  name: 'ItemComentarios',
  props: ['comentario'],
  data() {
    return {
      edicao: false,
    }
  },

  computed: {
    ...mapState(['usuario']),

    edit() {
      let condicao = false
      if (this.usuario.id == this.comentario.usuarioId) {
        condicao = true
      } else {
        condicao = false
      }
      return condicao
    },
  },
}
