import { baseApiUrl } from '@/global'
import axios from 'axios'
export default {
  data() {
    return {
      categorias: [],
    }
  },

  methods: {
    getCategorias() {
      const url = ` ${baseApiUrl}/categorias`
      axios(url).then((res) => {
        this.categorias = res.data
      })
    },
  },

  mounted() {
    this.getCategorias()
  },
}
