import { mapState } from 'vuex'
import { baseApiUrl } from '@/global'
import axios from 'axios'
import ItemNotas from './ItemNotas'
export default {
  name: 'Notas',
  components: { ItemNotas },
  data() {
    return {
      edit: false,
      notas: [],
      notabyId: {},
      nota: {},
    }
  },
  methods: {
    getNotas() {
      const url = ` ${baseApiUrl}/${this.usuario.user}/notas`
      axios(url).then(res => {
        this.notas = res.data
      })
    },
  },

  // watch: {
  //   notas() {
  //     this.getNotas()
  //   },
  // },

  computed: { ...mapState(['usuario']) },
  mounted() {
    this.getNotas()
  },
}
