import ItemObras from './ItemObras'
import { mapState } from 'vuex'
import axios from 'axios'
import { baseApiUrl } from '../../global'
export default {
  name: 'obrasPublicas',
  components: { ItemObras },
  data() {
    return {
      obras: [],
      search: 'A',
      page: 1,
      limit: 0,
      count: 0,
      totalPage: 0,
      status: [
        { id: 'A', text: 'Em Andamento' },
        { id: 'T', text: 'Terminada' },
        { id: 'S', text: 'Suspensa' },
      ],
    }
  },

  computed: {
    filteredItems() {
      const id = this.search

      const buscar = this.status.filter((e) => {
        return e.id === id
      })
      return buscar
    },

    filtershelves() {
      const a = this.obras.filter((e) => {
        return e.terminada === this.search
      })
      return a
    },

    ...mapState(['usuario']),
  },

  methods: {
    getObrasPublicas() {
      const url = ` ${baseApiUrl}/mesa/${this.$store.state.usuario.id}/obraspublicas`
      axios(url).then((res) => {
        this.obras = res.data
      })
    },
  },

  mounted() {
    this.getObrasPublicas()
  },
}
