import { baseApiUrl, showError } from '../../global'
import axios from 'axios'
export default {
  name: 'Cadastro',
  data() {
    return {
      show1: false,
      usuario: {},
      cadastro: false,
      dialog: false,

      user: {
        user: '',
        email: '',
      },
      nameRules: [(value) => !!value || 'Digite seu nome'],
    }
  },
  methods: {
    save() {
      axios
        .post(`${baseApiUrl}/usuarios`, this.usuario)
        .then(() => {
          this.email = ''
          this.dialog = 'true'
        })
        .catch(showError)
    },

    getUser() {
      axios.get(`${baseApiUrl}/usuario/${this.usuario.user}/user`).then((res) => {
        this.user.user = res.data.user
      })
    },
    GetEmail() {
      axios.get(`${baseApiUrl}/usuario/${this.usuario.email}/email`).then((res) => {
        this.user.email = res.data.email
      })
    },

    continuar() {
      this.dialog = false

      this.cad = false
    },
  },

  computed: {
    GetCadastro() {
      return this.$store.state.cadastro
    },

    passwordConfirmationRule() {
      return this.usuario.password === this.usuario.confirmarPassword || 'Senhas no conferem'
    },
    checkUser() {
      if (this.usuario.user) {
        return this.usuario.user !== this.user.user || 'Usurio j est sendo utilizado'
      } else {
        return 'Digite um usurio'
      }
    },
    checkEmail() {
      if (this.usuario.email) {
        return this.usuario.email !== this.user.email || 'Este e-mail j est cadastrado'
      } else {
        return 'Digite seu e-mail'
      }
    },

    cad: {
      get() {
        return this.$store.state.cadastro
      },
      set(valor) {
        this.$store.commit('setCadastro', valor)
      },
    },

    ckeckUser() {
      if (this.user) {
        return 'Usuario j est sendo usado'
      } else {
        return {}
      }
    },
  },

  // mounted() {
  //   this.getUser()
  // },
}
