import { baseApiUrl, showError } from '@/global'
import axios from 'axios'
export default {
  name: 'Imagens',
  props: ['usuario'],
  data() {
    return {
      urlPerfil: null,
      urlBanner: null,
      imagemPerfil: [],
      imagemBanner: [],
      url: baseApiUrl,
      user: '',
    }
  },

  methods: {
    onFileChangePerfil(e) {
      const file = e.target.files[0]
      this.urlPerfil = URL.createObjectURL(file)
    },
    onFileChangeBanner(e) {
      const file = e.target.files[0]
      this.urlBanner = URL.createObjectURL(file)
    },
    openFileDialogPerfil() {
      document.getElementById('file-upload-perfil').click()
    },
    openFileDialogBanner() {
      document.getElementById('file-upload-banner').click()
    },

    savePerfil() {
      var fd = new FormData()
      fd.append('file', this.imagemPerfil)

      axios
        .post(`${baseApiUrl}/perfil/${this.usuario.id}/upload`, fd)
        .then(() => {
          // eslint-disable-next-line no-console

          this.$toast.success('Imagem alterada com sucesso')
        })
        .catch(showError)
    },
    saveBanner() {
      var fd = new FormData()
      fd.append('file', this.imagemBanner)
      axios
        .post(`${baseApiUrl}/banner/${this.usuario.id}/upload`, fd)
        .then(() => {
          this.$toast.success('Imagem alterada com sucesso')
        })
        .catch(showError)
    },
  },

  mounted() {
    this.user = this.$store.getters.user
  },
}
