import { mapState } from 'vuex'
import { userKey, baseApiUrl } from '@/global'

export default {
  name: 'Menu',
  computed: mapState(['isMenuVisible', 'usuario']),
  miniNovo: {
    get() {
      return this.$store.state.drawer
    },
    set(valor) {
      this.$store.commit('setDrawer', valor)
    },
  },

  data: () => ({
    mini: true,
    nome: {},
    usuarios: [],
    drawer: false,
    expandNews: false,
    expandSingle: true,
    dialog: false,
    url: baseApiUrl,

    items: [
      {
        icon: 'mdi-account',
        text: 'Meu Perfil',
        path: 'perfil',
        notificacao: 0,
        acao: '',
      },
      {
        icon: 'mdi-bookshelf',
        text: 'Minha Estante',
        path: 'minhaEstante',
        notificacao: 3,
        acao: '',
      },

      {
        icon: 'mdi-desk',
        'icon-alt': 'mdi-chevron-down',
        text: 'Minha Mesa',
        notificacao: 0,
        acao: '',
        model: false,
        children: [
          {
            icon: 'mdi-lock-open-variant',
            text: 'Obras Pblicas',
            path: 'obrasPublicas',
          },
          { icon: 'mdi-lock', text: 'Obras Privadas', path: 'obrasPrivadas' },
          { icon: 'mdi-plus', text: 'Adicionar Obra', path: 'adicionarObra' },
          {
            icon: 'mdi-pencil-box-multiple',
            text: 'Minhas Notas',
            path: 'notas',
          },
        ],
      },
      { icon: 'mdi-comment', text: 'Comentrios', notificacao: 3, acao: '' },
      { icon: 'mdi-send', text: 'Mensagens', notificacao: 10, acao: '' },
      { icon: 'mdi-help-circle', text: 'Help', notificacao: 0, acao: '' },
      { icon: 'mdi-cog', text: 'Configuraes', path: 'Configuracoes', notificacao: 0, acao: '' },
    ],
  }),

  methods: {
    logout() {
      localStorage.removeItem(userKey)
      this.$store.commit('setUser', null)
      this.$router.push({ name: 'login' })
      this.dialog = false
    },
    fechar() {
      this.mini = !this.mini
      this.expandNews = false
    },
  },
}
