import { baseApiUrl, showError } from '@/global'
import axios from 'axios'
export default {
  name: 'ItemEstante',
  props: ['item'],
  data() {
    return {
      dialog: false,
    }
  },

  methods: {
    remove() {
      const id = this.item.obraId
      axios
        .delete(`${baseApiUrl}/mesa/${id}`)
        .then(() => {
          this.$toasted.global.defaultSuccess()
        })
        .catch(showError)
    },
    onConfirm() {
      this.value = 'Agreed'
    },
    onCancel() {
      this.value = 'Disagreed'
    },
  },
}
